<template>
  <div class="wrapper">
    <div class="animated fadeIn">
      <b-card header="">
        <b-form-radio-group
          v-model="checklistTypeSelected"
          name="radioChecklistType"
          :options="checklistTypes"
        >
        </b-form-radio-group>
        <b-btn v-b-toggle.collapse1 variant="primary"> </b-btn>
        <b-collapse id="collapse1" class="mt-2">
          <b-card>
            <b-form>
                <b-form-group label="">
                  <b-row>
                    <b-col>
                      <v-select label="name"
                        placeholder=""
                      ></v-select>
                    </b-col>
                    <b-col>
                      <v-select label="name"
                        placeholder=""
                      ></v-select>
                    </b-col>
                    <b-col>
                      <v-select label="name"
                        placeholder=""
                      ></v-select>
                    </b-col>
                  </b-row>
                </b-form-group>
              <b-form-group label="">
                <b-form-radio-group
                  v-model="checklistQuizTypeSelected"
                  name="radioChecklistQuizType"
                  :options="checklistQuizTypes"
                ></b-form-radio-group>
              </b-form-group>
              <b-form-group label="">
                <b-form-input type="text" required placeholder=" "
                  v-model="form.question"
                >
                </b-form-input>
              </b-form-group>
              <b-form-group label="">
                <input-tag placeholder=" " :tags="sampleAnswers"></input-tag>
                <b-btn variant="link" size="sm" @click="reversSampleAnswers"></b-btn>
              </b-form-group>
            </b-form>
          </b-card>
        </b-collapse>
      </b-card>
    </div>
  </div>
</template>

<script>
export default {
  name: 'checklist-item',

  components: {
  },

  data () {
    return {
      checklistTypeSelected: 'SV',
      checklistQuizTypeSelected: 'S',
      checklistTypes: [
        { text: '', value: 'SV' },
        { text: '', value: 'MS' }
      ],
      checklistQuizTypes: [
        { text: '', value: 'S' },
        { text: '', value: 'T' },
        { text: '', value: 'M' }
      ],
      form: {
        question: null
      },
      sampleAnswers: [ '1', '2', '3', '4', '5' ]
    }
  },

  methods: {
    reversSampleAnswers () {
      this.sampleAnswers.reverse()
    }
  },

  computed: {
  }
}
</script>


