<template>
  <div class="wrapper">
    <div class="animated fadeIn">
      <b-card header=" ">
        <b-btn v-b-modal.modalAddCategory variant="primary"> </b-btn>
        <b-btn v-b-modal.modalAddChecklist variant="primary"
          to="/checklist/create"
        > </b-btn>
      </b-card>
    </div>
    <!--   -->
    <b-modal id="modalAddCategory" title="" size="lg"
      :header-bg-variant="modalVariants.headerBgVariant"
      :header-text-variant="modalVariants.headerTextVariant"
      :no-close-on-backdrop="true"
      :no-close-on-esc="true"
      :ok-only="true"
      ok-title=""
    >
      <b-tabs pills>
        <b-tab title="" active>
          <category :category-type="categoryTypes.big.key"></category>
        </b-tab>
        <b-tab title="">
          <category :category-type="categoryTypes.middle.key"></category>
        </b-tab>
        <b-tab title="">
          <category :category-type="categoryTypes.small.key"></category>
        </b-tab>
      </b-tabs>
    </b-modal>
  </div>
</template>

<script>
import Category from '../components/Category'
import { mapGetters } from 'vuex'

export default {
  name: 'checklist',

  components: {
    Category
  },

  data () {
    return {
    }
  },

  methods: {
  },

  computed: {
    ...mapGetters({
      categoryTypes: 'categoryTypes',
      modalVariants: 'modalVariants'
    })
  }
}
</script>


